	module.exports = {"data":{"markdownRemark":{"html":"<p>I've finally pressed 'Submit for Review' on my first iOS app and the first app in my Year of Apps challenge. The app is a shape / colour matching game for iOS named Reaction Match.</p>\n<p><img src=\"/images/year-of-apps-reaction-match-logo.png\" alt=\"Reaction Match icon\"></p>\n<p>The theme of this first month was 'simple', to make sure I didn't fall at the first hurdle I have kept the concept simple and the scope as small as possible.</p>\n<p>The game presents the player with 5 coloured squares, with the aim of moving the centre square to the outer square of the same colour before the timer runs out. Each successful match can earn the player up to 10 points depending on how quick they are. The amount of time for each match is reduced every turn, making higher scores ever harder to get. Once time runs out or an incorrect match is made the game is over.</p>\n<p>Although the concept of the game is a simple one, I still wanted the app to be a challenge to create. I already knew of one challenge I would face - finding the time. I spend the majority of my day at work, and once home - I have an excitable two year old to look after and a partner spend time with. The biggest success in this first month of the challenge has been finding 2 or 3 hours every night to work on these projects - without sacrificing my other responsibilities.</p>\n<p>The other challenge faced with this project was knowledge. While I’ve made iOS apps, experimented with Objective-C and used Xcode in the past; this is my first experience with Swift and SpriteKit, the framework used to build the game.</p>\n<p>The game is currently in review for the iOS app store, and I will post a link here once <em>(if)</em> its accepted.</p>\n<p>All of the apps I make in this series will be open source on GitHub. The source for Reaction Match can be found here <a href=\"https://github.com/eddielee6/ReactionMatch\">github.com/eddielee6/ReactionMatch</a>.</p>\n<p><img src=\"/images/year-of-apps-reaction-match-screenshot.jpg\" alt=\"Reaction Match Screenshot\"></p>","timeToRead":1,"excerpt":"I've finally pressed 'Submit for Review' on my first iOS app and the first app in my Year of Apps challenge. The app is a shape / colour…","frontmatter":{"title":"A Year of Apps #1 - Reaction Match","cover":"https://unsplash.it/1152/300/?random?Cards","date":"2016-01-28T19:18","tags":["A Year of Apps","Reaction Match"],"author":"eddie"},"fields":{"slug":"/a-year-of-apps-1-reaction-match"}},"prev":{"excerpt":"Reaction Match is now available to download on the iOS App Store  https://itunes.apple.com/gb/app/reaction-match…","frontmatter":{"title":"Presenting Reaction Match","cover":"https://unsplash.it/1152/300/?random?Release","date":"2016-02-03T16:59"},"fields":{"slug":"/presenting-reaction-match"}},"next":{"excerpt":"","frontmatter":{"title":"James Cameron's advice on becoming a film director applies to new game developers.","cover":"https://unsplash.it/1152/300/?random?Film","date":"2016-01-26T22:35"},"fields":{"slug":"/james-camerons-advice-on-becoming-a-film-director"}},"authors":{"edges":[{"node":{"id":"eddie","name":"Eddie Lee","image":"/images/eddielee.jpg","url":"https://twitter.com/eddielee6","bio":"Father and Software Engineer"}}]}},"pathContext":{"slug":"/a-year-of-apps-1-reaction-match","total":46,"prev":"/presenting-reaction-match","next":"/james-camerons-advice-on-becoming-a-film-director"}}

